@import 'common';

// top  //
.top {
  @include children-center;
  @include max-w;
  height: $top-height;
  position: relative;
  @include mq($desktop-width) {
    margin: 0 auto;
    width: $desktop-width; } }

.top-bg {
  @include max-w;
  background-image: linear-gradient(to top, $color-top-start, $color-top-middle , $color-top-end);
  height: $top-height;
  position: absolute;
  @include mq($desktop-width) {
    width: $desktop-width; } }

.theme-wrapper {
  @include children-center;
  @include vertical-center($top-height);
  width: 100%;
  @include mq($desktop-width) {
    left: 100px;
    width: 360px; } }

.theme-text {
  color: $white;
  font-size: 34.13px;
  letter-spacing: .89px;
  text-shadow: 0 2px 4px rgba(0, 0, 0, .9);
  @include mq($desktop-width) {
    font-size: 34.13px; } }

.theme-sub-text {
  color: $white;
  letter-spacing: .89px;
  margin: 0 auto 30px;
  text-shadow: 0 2px 4px rgba(0, 0, 0, .9);
  @include mq($desktop-width); }



.theme-button-top {
  @extend %theme-button;
  @include mq($desktop-width) {
    visibility: hidden; } }

%svg-wave {
  bottom: 0;
  left: 50%;
  position: absolute;
  transform: translate3d(-50%, 0, 0);
  width: 100%; }


.svg-wave-front {
  @extend %svg-wave; }

.svg-wave-back-small {
  @extend %svg-wave; }

.svg-wave-back-big {
  @extend %svg-wave; }

.svg-camome {
  display: none;
  @include mq($desktop-width) {
    display: inline;
    height: 537px;
    left: 568px;
    position: absolute;
    top: 10px;
    visibility: visible;
    width: 449px; } }

.svg-camome-main {
  display: none;
  @include mq($desktop-width) {
    display: inline;
    left: 568px;
    position: absolute;
    visibility: visible;
    top: 10px; } }

//^^^ top ^^^//

// middle //
.middle {
  @include max-w;
  @include mq($desktop-width) {
    height: 512px;
    margin: 0 auto;
    width: $desktop-width; } }

.title-middle {
  @include max-w;
  @include children-center;
  @include mq($desktop-width) {
    display: table-cell;
    height: 166px;
    margin: 0 auto;
    text-align: center;
    vertical-align: middle;
    width: $desktop-width; } }

$img-margin-right: 5px;
$middle-img-width: 324px;

.middle-rapper {
  @include max-w;
  @include children-center;
  @include mq($desktop-width) {
    display: flex;
    height: 500px;
    justify-content: center; }

  div {
    position: relative;
    width: $middle-img-width;
    margin: 0 auto;
    @include mq($desktop-width) {
      height: 224px;
      border-bottom: 0;
      border-right: 2px solid $gray;
      margin: 0;
      margin-right: $img-margin-right; }

    &:last-child {
      border-bottom: 0;
      @include mq($desktop-width) {
        border-right: 0;
        margin-right: 0; } } }


  .svg-vec {
    left: 0;
    position: absolute;
    top: 0; }

  .middle-text {
    font-size: 15px;
    left: 50%;
    letter-spacing: .2em;
    position: absolute;
    top: 160px;
    transform: translate3d(-50%, 0, 0);
    color: $color-text;
    width: $middle-img-width * .8; } }




//^^^ middle ^^^//

// under
.under {
  @include children-center;
  @include max-w;
  background-image: linear-gradient(-180deg, $white 0%, $color-under-end 100%);
  height: 512px;
  position: relative;
  margin: 0 auto;
  @include mq($desktop-width) {
    width: $desktop-width; }

  .under-wrapper {
    @include max-w;
    @include vertical-center($top-height);
    @include mq($desktop-width) {
      width: $desktop-width; } } }

.under-text {
  color: $white;
  font-size: 35.56px;
  letter-spacing: .93px;
  text-shadow: 0 2px 6px #4F7A7A; }

.theme-button-under {
  @extend %theme-button;
  @include mq($desktop-width) {
    margin: 50px 0; } }

//^^^under
